@import "./variables.css";

/* movies  */
/*  movies list wrapper */
.collection-wrapper {
  text-align: center; /* center all the contnets */
}

.collection-wrapper.light {
  background-color: var(--bg-light);
}

.collection-wrapper.dark {
  background-color: var(--bg-dark);
}

/* just a wraper to  */
.movies {
  align-items: center;
  padding: 0px;
  margin-top: 0;
}

/* movie box */
/* box */
.movies .movie-box {
  display: inline-block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  margin-top: 10px;
  margin-right: 10px;
  border: 0px solid;
  border-radius: 6px 6px 3px 3px;
}

.movies .movie-box.light {
  background-color: var(--ui-elemnet-light);
  box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 8px 0 rgba(0, 0, 0, 0.19);
}

.movies .movie-box.dark {
  background-color: var(--ui-elemnet-dark);
  box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.18), 0 6px 8px 0 rgba(0, 0, 0, 0.167);
}

/* titile */
.movies .movie-title {
  padding: 10px;
  width: 157px;
  white-space: nowrap;
  overflow: hidden;
}

.movies .movie-title.light {
  /* color: #5107c0; */
  color: var(--font-color-light);
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 400;
  border-top: 3px solid var(--font-color-light);
}

.movies .movie-title.dark {
  color: var(--font-color-dark);
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 400;
  border-top: 3px solid var(--font-color-dark);
}



.movies .movie-cover img,
.movies .movie-nocover {
  width: 157px;
  height: 233px;
  overflow: hidden;
  border-radius: 5px 5px 0 0;
}

.movie-cover .movie-nocover {
  overflow: hidden;
}

.movie-cover .movie-nocover.light {
  /* color: #5107c0; */
  color: var(--font-color-light);
  border: 1px solid var(--font-color-light);
  /* background-color: rgb(194, 196, 197); */
  background-color: var(--bg-light);
}

.movie-cover .movie-nocover.dark {
  color: var(--font-color-dark);
  border: 1px solid var(--font-color-dark);
  /* background-color: #252323; */
  background-color: var(--bg-dark);
}

@media (max-width: 400px) {
  .movies .movie-box {
    margin-top: 10px;
    margin-right: 10px;
  }

  .movies .movie-cover img,
  .movies .movie-nocover {
    width: 137px;
    height: 137px;
  }

  .movies .movie-title {
    width: 137px;
  }
}

@media (min-width: 420px) {
  .movies .movie-box {
    margin-top: 10px;
    margin-right: 10px;
  }

  .movies .movie-cover img,
  .movies .movie-nocover {
    width: 147px;
    height: 147px;
  }

  .movies .movie-title {
    width: 147px;
  }
}


@media (min-width: 1080px) {
  .movie-box.light:hover {
    border-radius: 5px 5px 10px 10px;    
    transform-origin: top bottom right left;
    /* transform: scale(1.0);  */
    background-color: var(--ui-elemnet-dark);
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.18), 0 6px 8px 0 rgba(0, 0, 0, 0.167);
    transition: transform 1s, border-radius var(---hover-movies), background-color var(---hover-movies), box-shadow var(---hover-movies);
  }

  .movie-box.dark:hover {
    border-radius: 6px 6px 10px 10px;    
    transform-origin: top bottom right left;
    /* transform: scale(1.0);  */
    background-color: var(--ui-elemnet-light);
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 8px 0 rgba(0, 0, 0, 0.19);
    transition: transform 1s, border-radius var(---hover-movies), background-color var(---hover-movies), box-shadow var(---hover-movies);
  }

  .movie-box:hover .movie-title.dark{
    color: var(--font-color-light);
    border-top: 3px solid var(--font-color-light);
    transition: color var(---hover-movies), border-top var(---hover-movies);

  }

  .movie-box:hover .movie-title.light{
    color: var(--font-color-dark);
    border-top: 3px solid var(--font-color-dark);
    transition: color var(---hover-movies), border-top var(---hover-movies);
    
  }

  .movies .movie-box {
    margin-top: 10px;
    margin-right: 20px;
  }

  .movies .movie-cover img,
  .movies .movie-nocover {
    width: 200px;
    height: 230px;
  }

  .movies .movie-title {
    width: 200px;
  }

}
